<template>
  <div id="user">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="/home"> 首页 </Breadcrumb-item>
        <Breadcrumb-item> 小说管理 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <i-col span="4">
          <i-button type="primary" @click.native="showNew" style="float: left;">
            <Icon type="plus-round"></Icon>
            新建小说
          </i-button>
        </i-col>
        <i-col span="20">
          <i-form :model="formInline" inline style="float: right;">
            <Radio-group v-model="formInline.field" type="button">
              <Radio label="id">小说编号</Radio>
              <Radio label="title">小说英文名</Radio>
              <Radio label="chinese_title">小说中文名</Radio>
            </Radio-group>
            <Form-item>
              <i-input type="text" v-model="formInline.value" placeholder="按所选条件进行搜索"></i-input>
            </Form-item>
            <Form-item>
              <i-select v-model="formInline.sort_id" style="width: 100px;">
                <i-option :value="-1">全部分类</i-option>
                <i-option v-for="item in sortList" :value="item.id">{{ item.chinese_name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item>
              <i-select v-model="formInline.status" style="width: 100px;">
                <i-option v-for="item in statusList" :value="item.status">{{ item.name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item>
              <i-button type="success" @click.native="handleSubmit()"> 搜索 </i-button>
            </Form-item>
          </i-form>
        </i-col>
        <i-col span="24">
          <div class="ivu-table-wrapper">
            <div class="ivu-table ivu-table-border ivu-table-stripe">
                <table cellspacing="0" cellpadding="0" border="0" style="width: 100%;">
                  <thead class="ivu-table-header">
                    <tr class="ivu-table-row">
                      <th class="ivu-table-column-center" width="8%"><div class="ivu-table-cell"><span>编号</span></div></th>
                      <th class="ivu-table-column-center" width="26%"><div class="ivu-table-cell"><span>全称(英文名)</span></div></th>
                      <th class="ivu-table-column-center" width="9%"><div class="ivu-table-cell"><span>分类</span></div></th>
                      <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>连载状态</span></div></th>
                      <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>发布状态</span></div></th>
                      <th class="ivu-table-column-center" width="12%"><div class="ivu-table-cell"><span>最后操作用户</span></div></th>
                      <th class="ivu-table-column-center" width="25%"><div class="ivu-table-cell"><span>操作</span></div></th>
                    </tr>
                  </thead>
                  <tbody class="ivu-table-tbody">
                  <tr class="ivu-table-row" v-for="(item, index) in list">
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.id }}</span></div></td>
                    <td class=""><div class="ivu-table-cell"><div><a href="javascript:;" @click="showNovel(index)">{{ item.title }}({{ item.chinese_title }})</a></div></div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ getSortName(item.sort_id) }}</span></div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">
                      <span class="text-success" v-if="item.public_status === 1">已完结</span>
                      <span class="text-warning" v-else>连载中</span>
                    </div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">
                      <div v-if="parseInt(item.status) === 1"><Tooltip trigger="hover" :content="getTime(item.publish_at)" placement="left"><Tag type="border" color="green">已发布</Tag></Tooltip></div>
                      <div v-else-if="parseInt(item.status) === 2"><Tooltip trigger="hover" :content="getTime(item.publish_at)" placement="left"><Tag type="border" color="blue">定时发布</Tag></Tooltip></div>
                      <div v-else-if="parseInt(item.status) === 99"><Tag type="border" color="red">下架</Tag></div>
                      <div v-else><Tooltip trigger="hover" :content="getTime(item.create_at)" placement="left"><Tag type="border">待审核</Tag></Tooltip></div>
                    </div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.auditor }}</span></div></td>
                    <td class="ivu-table-column-right">
                      <div class="ivu-table-cell">
                        <div>
                          <button v-if="parseInt(item.status) === 0" type="button" class="ivu-btn ivu-btn-success ivu-btn-small" @click="showAudit(index)"><span>审核</span></button>
                          <button type="button" class="ivu-btn ivu-btn-info ivu-btn-small" @click="showEdit(index)"><span>修改</span></button>
                          <button type="button" class="ivu-btn ivu-btn-warning ivu-btn-small" @click="handleChapter(index)"><span>章节管理</span></button>
                          <button type="button" class="ivu-btn ivu-btn-error ivu-btn-small" @click="showDelete(index)"><span v-if="parseInt(item.status) === 99">清退</span><span v-else>下架</span></button>
                        </div>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
            </div>
          </div>
        </i-col>
        <div class="table-page">
          <Page :total="total" show-elevator @on-change="getList"></Page>
        </div>
      </Card>
    </Row>

    <Modal
      title="小说审核"
      v-model="showModal"
      :mask-closable="false"
      :loading="true"
      @on-ok="postModal"
      width="500">
      <Row>
        <i-col span="20" offset="2">
          <i-form v-model="handleModal" :label-width="100">
            <Form-item label="小说中文名">
              <i-input v-model="handleModal.chinese_title" disabled></i-input>
            </Form-item>
            <Form-item label="审核方式">
              <Radio-group v-model="handleModal.status" type="button" size="small" style="float: left;">
                <Radio label="1">立即发布</Radio>
                <Radio label="2">定时发布</Radio>
                <Radio label="99" v-show="parseInt(handleModal.status) === 99">下架待清退</Radio>
              </Radio-group>
            </Form-item>
            <div v-show="handleModal.status==2">
              <Form-item label="定时审核时间">
                <Date-picker v-model="handleModal.publish_date" type="datetime" format="yyyy-MM-dd HH:mm:ss" placeholder="定时发布日期和时间" width="200px" @on-change="changeDate"></Date-picker>
              </Form-item>
            </div>
          </i-form>
        </i-col>
      </Row>
    </Modal>

    <Modal
      title="小说详情"
      v-model="showNovelModal"
      :mask-closable="false"
      width="600">
      <div class="novel-show-table">
        <table border="0" cellpadding="0" cellspacing="0">
          <thead>
          <tr>
            <td>字段</td>
            <td>内容</td>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>文章编号</td>
            <td>{{ novelInfo.id }}</td>
          </tr>
          <tr>
            <td>中文名称(英文名称)</td>
            <td>{{ novelInfo.chinese_title }}({{ novelInfo.title }})</td>
          </tr>
          <tr>
            <td>小说简介</td>
            <td>{{ novelInfo.intro }}</td>
          </tr>
          <tr>
            <td>小说分类</td>
            <td>{{ getSortName(novelInfo.sort_id) }}</td>
          </tr>
          <tr>
            <td>作者</td>
            <td>{{ novelInfo.author }}</td>
          </tr>
          <tr>
            <td>连载状态</td>
            <td v-if="parseInt(novelInfo.public_status) === 1">已完结</td>
            <td v-else>连载中</td>
          </tr>
          <tr>
            <td>章节数</td>
            <td>{{ novelInfo.chapter_count }}</td>
          </tr>
          <tr>
            <td>总字数</td>
            <td>{{ novelInfo.size }}</td>
          </tr>
          <tr>
            <td>发布状态(发布时间)</td>
            <td v-if="novelInfo.status === 0">待审核</td>
            <td v-else-if="novelInfo.status === 1">已发布({{ getTime(novelInfo.publish_at) }})</td>
            <td v-else-if="novelInfo.status === 2">定时发布({{ getTime(novelInfo.publish_at) }})</td>
            <td v-else>下架</td>
          </tr>
          <tr>
            <td>审核人</td>
            <td>{{ novelInfo.auditor }}</td>
          </tr>
          <tr>
            <td>创建时间</td>
            <td>{{ getTime(novelInfo.create_at) }}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </Modal>
  </div>
</template>

<script>
  import moment from 'moment'

  export default {
    data () {
      return {
        self: this,
        total: 0,
        formInline: {
          field: 'id',
          value: '',
          status: -1,
          sort_id: -1
        },
        showModal: false,
        showNovelModal: false,
        modalTitle: '-',
        novelInfo: {},
        handleModal: {
          id: 0,
          chinese_title: '',
          status: 1,
          publish_at: 0,
          publish_date: ''
        },
        statusList: [
          { status: -1, name: '全部状态' },
          { status: 0, name: '待审核' },
          { status: 1, name: '已发布' },
          { status: 2, name: '定时发布' },
          { status: 99, name: '下架' }
        ],
        sortList: [],
        list: []
      }
    },
    created () {
      this.initList()
    },
    watch: {
      '$watch': 'initList'
    },
    methods: {
      showNew () {
        return this.$router.push('/novel_new')
      },
      showEdit (index) {
        return this.$router.push({ path: '/novel_edit', query: { id: this.list[index].id } })
      },
      showDelete (index) {
        let deleteTypeTitle = parseInt(this.list[index].status) === 99 ? '清退' : '下架'
        this.$Modal.confirm({
          title: '确定' + deleteTypeTitle + '?',
          content: '那就' + deleteTypeTitle + '吧~',
          loading: true,
          onOk: () => {
            this.$http.post('/admin_api/novel/delete', { id: this.list[index].id }).then(response => {
              if (response.body.error === 0) {
                this.list[index] = {}
                this.$Message.success(response.body.message)
                this.$Modal.remove()
              } else {
                this.$Notice.error({ 'title': '请求失败', 'desc': response.body.message })
              }
            })
          },
          cancelText: '取消'
        })
      },
      showNovel (index) {
        this.novelInfo = this.list[index]
        this.showNovelModal = true
      },
      getList (page) {
        this.$http.post('/admin_api/novel?page=' + page, this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      },
      postModal () {
        this.$http.post('/admin_api/novel/audit', this.handleModal).then(response => {
          if (response.body.error === 0) {
            this.$Message.success(response.body.message)
            this.$Modal.remove()
            this.$router.go(0)
          } else {
            this.$Notice.error({ 'title': '审核失败', 'desc': response.body.message })
          }
        })
      },
      handleChapter (index) {
        sessionStorage.setItem('novel', JSON.stringify(this.list[index]))
        return this.$router.push({ path: '/novel_chapter', query: { id: this.list[index].id } })
      },
      handleSubmit () {
        this.$http.post('/admin_api/novel', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      },
      showAudit (index) {
        this.handleModal.id = this.list[index].id
        this.handleModal.chinese_title = this.list[index].chinese_title
        this.handleModal.publish_date = ''
        this.handleModal.status = this.list[index].status === 0 ? 1 : this.list[index].status
        this.showModal = true
      },
      changeDate (timeString) {
        let changeTime = timeString
        this.handleModal.publish_date = changeTime
      },
      getSortName (sortId) {
        for (let index = 0; index < this.sortList.length; index++) {
          if (sortId === this.sortList[index].id) {
            return this.sortList[index].chinese_name
          }
        }
        return '-'
      },
      getTime (date, $format = 'YYYY-MM-DD h:mm') {
        return moment.unix(date).format($format)
      },
      initList () {
        this.$http.post('/admin_api/novel', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.sortList = response.body.data.sort_list
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .novel-show-table {
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  .novel-show-table table {
    overflow: hidden;
    width: 100%;
    border: 1px solid #d7dde4;
  }
  .novel-show-table table tr {
    height: 32px;
    line-height: 32px;
  }
  .novel-show-table table tr td {
    font-size: 12px;
    color: #657180;
    text-align: left;
    padding: 0 10px;
  }
  .novel-show-table table tr td:first-child {
    font-size: 14px;
    color: #464c5b;
    text-align: center;
    width: 30%;
    min-width: 80px;
  }
  .novel-show-table table td {
    border: 1px solid #d7dde4;
  }
  .novel-show-table table thead {
    background-color: #f5f7f9;
  }
  .novel-show-table table thead tr td {
    font-size: 14px;
    text-align: center;
  }
  .text-success {
    color: #00cc66;
  }
  .text-warning {
    color: #ffad33;
  }
</style>



